import React from 'react';
import { Link } from 'react-router-dom';
import '@styles/scss/Header.scss'

const Header = () => {
    return (
        <>
            {/* Header */}
            <div id="header">
                <h1><Link to="/"></Link></h1>
                <ul className="util">
                    <li><Link to="/"></Link></li>
                </ul>
            </div>
            {/* //Header */}
        </>
    )
}

export default Header;