import React from 'react';
import { Steps, Button } from 'antd';

const step3List = [
    {
        link:'https://blog.naver.com/mintlight/223092518932', 
        btnText:'  ', 
        source:'NAVER - HUG      1 (  ,  ) by ' 
    },
    {
        link:'https://service.epost.go.kr/iservice/usr/trace/usrtrc001k01.jsp', 
        btnText:'()', 
    }
]
const step4List = [
    {
        link:'https://blog.naver.com/PostView.naver?blogId=rlawndbf1010&logNo=222803645940&proxyReferer=',
        btnText:' ', 
        source:'NAVER -     feat.HUG by '
    }
]

const Step1 = (props) => {
    // eslint-disable-next-line react/prop-types
    const {miniStep, onClickNextMiniStep, ButtonWrap, onClickNextContent} = props;
    return (
        <>
            <Steps
                progressDot
                current={miniStep}
                direction="vertical"
                items={[
                    {
                        title: (<><span onClick={() => onClickNextMiniStep(0)}>  4~6 ?</span></>),
                        description: (
                        <>
                            <p className="tipWrap"  onClick={() => onClickNextMiniStep(0)}>
                                    . <b className="red"></b> .<br />
                            </p>
                            <ButtonWrap
                                next={1}
                                text='4~6     .'
                                idx={0}
                            />
                        </>
                        ),
                    },
                    {
                        title: (<><span onClick={() => onClickNextMiniStep(1)}>   ,    ?</span></>),
                        description: (
                        <>
                            <p className="tipWrap"  onClick={() => onClickNextMiniStep(1)}>
                                 &apos; &apos;       .<br />
                                   <b className='red'>,    </b>    ,<br />
                                            .
                            </p>
                            <ButtonWrap
                                next={2}
                                idx={1}
                            />
                        </>
                        ),
                    },
                    {
                        title: (<><span onClick={() => onClickNextMiniStep(2)}> 4    .<br />   ?</span></>),
                        description: (
                        <>
                            <p className="tipWrap" onClick={() => onClickNextMiniStep(2)}>
                                     .<br />
                                <b className="red">    </b> .
                            </p>
                            <ButtonWrap
                                next={4}
                                idx={2}
                                list={step3List}
                                addBtn={<><Button onClick={() => onClickNextMiniStep(3)}>  </Button></>}
                            />                           
                        </>
                        ),
                    },
                    {
                        title: (<><span onClick={() => onClickNextMiniStep(3)}> </span></>),
                        description: (
                        <>
                            <p className="tipWrap" onClick={() => onClickNextMiniStep(3)}>
                                   <b>    </b>  <b className="red"></b> 
                                 <b className="red">  , (), ,  </b>  <br />
                                <b className="red"> </b> 
                                   <b className="red"></b>        .<br />

                                   <b className="red"> </b>     .
                                   ?   .<br />
                                <b className="red">3     </b>      .<br />

                                 <b> (   )  2   3</b> .
                            </p>
                            <ButtonWrap
                                next={4}
                                idx={3}
                                oktext='/ .'
                                list={step4List}
                            />                           
                        </>
                        ),
                    },
                    {
                        title: (<><span onClick={() => onClickNextMiniStep(4)}>  ,   </span></>),
                        description: (
                        <>
                            <p className="tipWrap"  onClick={() => onClickNextMiniStep(4)}>
                                       <b>()</b>  .<br />
                                <b className="red">    ()  </b>   .
                            </p>
                            <ButtonWrap
                                next={5}
                                idx={4}
                                // oktext=''
                                addBtn={<Button onClick={() => onClickNextContent(1)}></Button>}
                            />      
                        </>
                        ),
                    },
                    
                   
                ]}
            />
        </>
    )
}

export default Step1;